import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Home from "./bridge-in-tech-web/home/Home";
import Members from "./bridge-in-tech-web/members/Members";
import MemberPortfolio from "./bridge-in-tech-web/members/MemberPortfolio";
import Member from "./bridge-in-tech-web/members/Member";
import Portfolio from "./bridge-in-tech-web/myspace/Portfolio";
import ErrorPage from "./bridge-in-tech-web/errorPage/ErrorPage"
import PersonalDetails from "./bridge-in-tech-web/myspace/PersonalDetails";
import AdditionalInfo from "./bridge-in-tech-web/myspace/AdditionalInfo";
import PersonalBackground from "./bridge-in-tech-web/myspace/PersonalBackground";
import Organizations from "./bridge-in-tech-web/organizations/Organizations";
import Organization from "./bridge-in-tech-web/organizations/Organization";
import EditPrograms from "./bridge-in-tech-web/myorganization/EditPrograms";
import EditOrganization from "./bridge-in-tech-web/myorganization/EditOrganization";
import EditProgram from "./bridge-in-tech-web/myorganization/EditProgram";
import Program from "./bridge-in-tech-web/organizations/Program";
import Register from "./bridge-in-tech-web/register/Register";
import Login from "./bridge-in-tech-web/login/Login";
import ProtectedRoute from "./ProtectedRoute";
import AuthProvider from "./AuthContext";
import Programs from "./bridge-in-tech-web/organizations/Programs";
import About from "./bridge-in-tech-web/home/About";
import Benefit from "./bridge-in-tech-web/home/Benefit"
import Nav from "./Navigation"

export default function Routes() {
  return (
    <Router>
        <AuthProvider>
          <Nav/>
        <Switch>
          <Route exact path="/">
            <Home />
          </Route>
          <Route exact path="/bridge-in-tech-web/about">
            <About />
          </Route>
          <Route exact path="/bridge-in-tech-web/benefit">
            <Benefit />
          </Route>
          <ProtectedRoute exact path="/bridge-in-tech-web/members">
            <Members />
          </ProtectedRoute>
          <ProtectedRoute path="/bridge-in-tech-web/members/portfolio/">
            <MemberPortfolio />
          </ProtectedRoute>
          <ProtectedRoute path="/bridge-in-tech-web/members/profile/">
            <Member />
          </ProtectedRoute>
          <ProtectedRoute exact path="/bridge-in-tech-web/organizations">
            <Organizations />
          </ProtectedRoute>
          <ProtectedRoute path="/bridge-in-tech-web/organizations/portfolio/">
            <Programs />
          </ProtectedRoute>
          <ProtectedRoute path="/bridge-in-tech-web/organizations/profile/">
            <Organization />
          </ProtectedRoute>
          <ProtectedRoute path="/bridge-in-tech-web/organizations/programs/">
            <Program />
          </ProtectedRoute>
          <ProtectedRoute exact path="/bridge-in-tech-web/portfolio">
            <Portfolio />
          </ProtectedRoute>
          <ProtectedRoute exact path="/bridge-in-tech-web/personal-details">
            <PersonalDetails />
          </ProtectedRoute>
          <ProtectedRoute exact path="/bridge-in-tech-web/additional-info">
            <AdditionalInfo />
          </ProtectedRoute>
          <ProtectedRoute exact path="/bridge-in-tech-web/personal-background">
            <PersonalBackground />
          </ProtectedRoute>
          <ProtectedRoute exact path="/bridge-in-tech-web/request-history"></ProtectedRoute>
          <ProtectedRoute exact path="/bridge-in-tech-web/organization-portfolio">
            <EditPrograms />
          </ProtectedRoute>
          <ProtectedRoute exact path="/bridge-in-tech-web/organization-profile">
            <EditOrganization />
          </ProtectedRoute>
          <ProtectedRoute path="/bridge-in-tech-web/organization-programs/">
            <EditProgram />
          </ProtectedRoute>
          <Route path="/bridge-in-tech-web/register">
            <Register />
          </Route>
          <Route path="/bridge-in-tech-web/login">
            <Login />
          </Route>
          <Route>
            <ErrorPage />
          </Route>
        </Switch>
      </AuthProvider>
    </Router>
  );
}
